@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i&display=swap");

body {
  font-family: "Open Sans", sans-serif;
}

.App {
  text-align: center;
  background: #f6f6f6;
}

.scene {
  border: 1px solid #e0e0e0;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  box-sizing: border-box;
  height: 100vh;
}

.scene-wrapper {
  position: relative;
  outline: none;
  user-select: none;
  width: calc(100% - 100px - 300px);
  height: calc(100vh - 71px);
  background: #e4e4e4;
  box-sizing: border-box;
}

.actions-wrapper {
  width: 100px;
  background: #f6f6f6;
  border-right: 2px solid #e0e0e0;
  border-bottom: 2px solid #e0e0e0;
  padding-top: 20px;
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
}

.topbar-wrapper {
  height: 70px;
  background: #f6f6f6;
  padding-left: 0px;
  padding-top: 0px;
  width: 100%;
  border-bottom: 2px solid #e0e0e0;
  box-sizing: border-box;
}

.toolbar-wrapper {
  background: #f6f6f6;
  width: 300px;
  border-left: 2px solid #e0e0e0;
  box-sizing: border-box;
}

/*












*/

.line {
  position: absolute;
  background: #5a5a5a;
  width: 0;
  height: 0;
}

.line-start {
  position: absolute;
  background: #5a5a5a;
}

.line-end {
  position: absolute;
  background: #5a5a5a;
}

.add-line-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: 100;
}

.bead-item {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  height: 20px;
  width: 20px;
  background: #bbb;
}

.button {
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 13px;
  color: #000;
}

.mouse-selection {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: 9;
}

.toolbar-wrapper .fieldset {
  /* border-top: 1px solid #dfdfdf;
  border-bottom: 1px solid #dfdfdf; */
  padding-bottom: 5px;
  padding-top: 5px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.toolbar-wrapper .fieldset .label {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 30px;
  font-weight: 300;
  padding: 0;
  margin: 0;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}

.toolbar-wrapper .fieldset .input {
  border: none;
  background: none;
  border-bottom: 1px solid #dfdfdf;
  color: #6e6e6e;
  font-size: 14px;
  line-height: 30px;
  font-family: "Open Sans", sans-serif;
  box-shadow: none;
  max-width: 200px;
  padding: 0;
  margin: 0;
}
.toolbar-wrapper .fieldset .input[type="number"] {
  max-width: 50px;
}

