  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}

.topbar-wrapper .fieldset .input {
  border: none;
  background: none;
  border-bottom: 1px solid #dfdfdf;
  color: #6e6e6e;
  font-size: 14px;
  line-height: 30px;
  font-family: "Open Sans", sans-serif;
  box-shadow: none;
  max-width: 200px;
